.history-item {
  position: relative;
  padding-left: 8.3%; }
  .history-item::before {
    content: "";
    position: absolute;
    top: 0.125rem;
    left: 2%;
    padding: 1.55%;
    background: #000000;
    border-radius: 50%; }
  .history-item__header {
    margin: 0;
    font-size: 1.71rem;
    line-height: 1;
    text-transform: uppercase; }
    .history-item__header_theme-blue {
      color: #0270ba; }
    .history-item__header_theme-black {
      color: #000000; }
  .history-item__dates {
    font-weight: normal;
    text-transform: none;
    color: #000000; }
  .history-item__second-title {
    display: block;
    margin-top: 0.459rem;
    font-weight: normal;
    color: #000000; }
    @media (max-width: 1280px) {
      .history-item__second-title {
        margin-top: 0; } }
  .history-item__text {
    margin: 0.5rem 0 0.9rem; }
