.main {
  display: flex;
  justify-content: space-between; }
  @media (max-width: 1024px) {
    .main {
      display: block; } }
  .main__left-column {
    margin-top: 4.4rem;
    margin-left: 4.5%;
    width: 33.4%; }
    @media (max-width: 1280px) {
      .main__left-column {
        width: 44%;
        margin-left: 2.5%; } }
    @media (max-width: 1024px) {
      .main__left-column {
        width: 90%;
        margin: 0 5%; } }
  .main__profile {
    margin-top: 4.75rem; }
  .main__contacts {
    margin-top: 3.2rem; }
  .main__skills {
    margin-top: 1rem; }
  .main__right-column {
    margin-top: 4.56rem;
    margin-right: 4.5%;
    width: 51.5%; }
    @media (max-width: 1440px) {
      .main__right-column {
        width: 50%; } }
    @media (max-width: 1280px) {
      .main__right-column {
        width: 44%;
        margin-right: 2.5%; } }
    @media (max-width: 1024px) {
      .main__right-column {
        width: 90%;
        margin: 3rem 5% 0; } }
  .main__experience {
    margin-top: 3.4rem; }
  .main__software {
    margin-top: 3.3rem; }
